import axios from "axios";
import fpjs from "@fingerprintjs/fingerprintjs";
import { EventEmitter } from "events";

const APIRoot = "https://api.tentuplay.io/v2021.4";

const TPAuthMode = "api_key_plain";
const TPHandshakeMode = "full";

export default class TentuPlay extends EventEmitter {
  "use strict";

  constructor({ clientKey, debug = false }) {
    super();
    this.clientKey = clientKey;
    this.debug = debug;
    this.axios = axios.create({ baseURL: APIRoot });
    let resolve;
    let reject;
    this.init = new Promise((res, rej) => {
      resolve = res;
      reject = rej;
    });

    const initPromises = [];

    // TODO: what to do if the runtime is Node.js?
    initPromises.push( // get browser fingerprint
      fpjs.load({ monitoring: false })
        .then(({ get }) => get())
        .then((res) => { this.fingerprint = res.visitorId; }),
    );
    initPromises.push( // get SDK version
      this.axios.get(
        "configs",
        {
          headers: {
            TPAuthMode,
            TPHandshakeMode,
            TPClientKey: this.clientKey,
          },
        },
      ).then((resp) => {
        this.SDKVersion = resp.data.data.sdk_version;
      }),
    );

    Promise.all(initPromises).then(() => {
      resolve();
      this.emit("init"); // Initialization done
    }).catch(() => reject());
  }

  sendJoin({ playerUUID }) {
    // what does "device" mean?
    // is it browser or machine?
    const now = new Date();
    const joinData = { players_join: [] };

    joinData.players_join.push({
      player_slug: playerUUID,
      device_unique_id: this.fingerprint,
      device_info: window.navigator.userAgent,
      event_timestamp: Math.floor(now / 1000),
      event_datetime: now,
      region: window.navigator.language, // FIXME:
    });

    return this.axios.post(
      "logs",
      JSON.stringify(joinData),
      {
        headers: {
          TPAuthMode,
          "Content-Type": "application/json",
          TPClientKey: this.clientKey,
        },
      },
    ).then((resp) => { // eslint-disable-line
      // console.log(resp.data);  // eslint-disable-line
      // TODO:
    });
  }

  sendLogin({ playerUUID, appVersion = null }) { // eslint-disable-line
    // TODO: validate parameters?
    const now = new Date();
    const payload = { players_login: [] };
    payload.players_login.push({
      player_slug: playerUUID,
      event_timestamp: now / 1000,
      sdk_version: this.SDKVersion,
      device_info: window.navigator.userAgent,
      device_unique_id: this.fingerprint, // FIXME:
      is_tp_debug_mode: this.debug,
      region: window.navigator.language,
    });

    return this.axios.post(
      "logs",
      payload,
      { headers: { TPAuthMode, TPClientKey: this.clientKey } },
    );
  }

  // it would be nice if we can use TypeScript
  sendPurchase({
    playerUUID,
    characterUUID,
    purchasableSlug,
    purchaseQuantity,
    purchaseUnitPrice,
    purchaseTotalPrice,
    purchaseCurrencyCode,
    isAdRemove = false,
  }) {
    const payload = { characters_purchases: [] };
    const timestamp = new Date() / 1000;
    payload.characters_purchases.push({
      player_slug: playerUUID,
      character_slug: characterUUID,
      purchasable_slug: purchasableSlug,
      purchase_quantity: purchaseQuantity,
      purchase_unit_price: purchaseUnitPrice,
      purchase_total_price: purchaseTotalPrice,
      purchase_currency_code: purchaseCurrencyCode,
      event_timestamp: timestamp,
      is_ad_remove: isAdRemove,
    });

    return this.axios.post(
      "logs",
      payload,
      { headers: { TPAuthMode, TPClientKey: this.clientKey } },
    ).then((resp) => { // eslint-disable-line
      // console.log(resp.data);  // eslint-disable-line
      // TODO:
    });
  }

  onInit(callback, timeout = 3000) {
    this.init.then(() => {
      callback();
    });
  }
}
